export async function createScene(engine, canvas, BABYLON) {
  console.clear();
  const scene = new BABYLON.Scene(engine);
  scene.useRightHandedSystem = true;

  var camera = new BABYLON.ArcRotateCamera("camera", BABYLON.Tools.ToRadians(90), BABYLON.Tools.ToRadians(65), 10, BABYLON.Vector3.Zero(), scene);
  camera.setTarget(new BABYLON.Vector3(3, 0.1, -1.75));
  camera.alpha = 1.578;
  camera.beta = 0.589;
  camera.radius = 44.48;
  camera.inertia = 0.4;
  camera.wheelDeltaPercentage = 0.3;
  camera.panningSensibility = 10000;
  camera.minZ = 0.01;
  camera.maxZ = 10000;
  camera.zoomToMouseLocation = true;
  camera.attachControl(canvas, true);

  var light = new BABYLON.HemisphericLight("light", new BABYLON.Vector3(0, 1, 0), scene);
  light.intensity = 0.1;

  var dirLight = new BABYLON.DirectionalLight("dirLight", new BABYLON.Vector3(0.36, -0.5, -0.79), scene);
  dirLight.position = new BABYLON.Vector3(-6.14, 24, 46.37);
  dirLight.diffuse = BABYLON.Color3.FromHexString("#FFF8E3");
  dirLight.intensity = 1;
  dirLight.shadowMaxZ = 300;

  var pointLight = new BABYLON.PointLight("pointLight", new BABYLON.Vector3(10.9, 5.87, -4.09), scene);
  pointLight.diffuse = BABYLON.Color3.FromHexString("#CF8015");
  pointLight.shadowMaxZ = 300;

  var spotLight = new BABYLON.SpotLight(
    "spotLight",
    new BABYLON.Vector3(-10.96, 10.2, -0.5),
    new BABYLON.Vector3(-0.32, -0.95, 0),
    Math.PI / 4,
    1.5,
    scene
  );
  spotLight.diffuse = BABYLON.Color3.FromHexString("#FF9500");
  spotLight.shadowMaxZ = 300;

  let meshes = [];

  var box = BABYLON.MeshBuilder.CreateBox("box", { size: 2 }, scene);
  box.position.y = 1;

  var box2 = box.clone("box2");
  box2.position.x = -15;

  var box3 = box.clone("box3");
  box3.position.x = 15;

  meshes.push(box);
  meshes.push(box2);
  meshes.push(box3);

  var ground = BABYLON.MeshBuilder.CreateGround("ground", { width: 64, height: 64 }, scene);
  ground.receiveShadows = true;

  let shadowGenerator_D = new BABYLON.ShadowGenerator(1024, dirLight);
  shadowGenerator_D.usePercentageCloserFiltering = true;
  shadowGenerator_D.filteringQuality = BABYLON.ShadowGenerator.QUALITY_HIGH;
  shadowGenerator_D.bias = 0.001;
  shadowGenerator_D._darkness = -1.0;

  let shadowGenerator_P = new BABYLON.ShadowGenerator(1024, pointLight);
  shadowGenerator_P.useExponentialShadowMap = true;
  shadowGenerator_P.depthScale = 300;
  shadowGenerator_P.filteringQuality = BABYLON.ShadowGenerator.QUALITY_HIGH;
  shadowGenerator_P.addShadowCaster(box3);
  shadowGenerator_P._darkness = -1.0;

  let shadowGenerator_S = new BABYLON.ShadowGenerator(1024, spotLight);
  shadowGenerator_S.usePercentageCloserFiltering = true;
  shadowGenerator_S.depthScale = 100;
  shadowGenerator_S.filteringQuality = BABYLON.ShadowGenerator.QUALITY_HIGH;
  shadowGenerator_S.addShadowCaster(box2);
  shadowGenerator_S._darkness = -1.0;

  meshes.forEach((m) => {
    shadowGenerator_D.addShadowCaster(m);
  });

  var advancedTexture = BABYLON.GUI.AdvancedDynamicTexture.CreateFullscreenUI("GUI");

  let darkness = 0;

  var panel = new BABYLON.GUI.StackPanel("panel");
  panel.width = "200px";
  panel.height = "400px";
  panel.left = "-20px";
  panel.top = "220px";
  panel.isVertical = true;
  panel.horizontalAlignment = BABYLON.GUI.Control.HORIZONTAL_ALIGNMENT_RIGHT;
  panel.verticalAlignment = BABYLON.GUI.Control.VERTICAL_ALIGNMENT_CENTER;
  advancedTexture.addControl(panel);

  var darknessTextBlock = new BABYLON.GUI.TextBlock("DarknessTB");
  darknessTextBlock.text = "Darkness: " + darkness.toFixed(2);
  darknessTextBlock.textHorizontalAlignment = BABYLON.GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  darknessTextBlock.color = "Black";
  darknessTextBlock.fontSize = 18;
  darknessTextBlock.height = "30px";
  darknessTextBlock.width = "200px";
  darknessTextBlock.horizontalAlignment = BABYLON.GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  darknessTextBlock.verticalAlignment = BABYLON.GUI.Control.VERTICAL_ALIGNMENT_CENTER;
  panel.addControl(darknessTextBlock);

  var darknessSlider = new BABYLON.GUI.Slider("darknessSlider");
  darknessSlider.name = "Darkness";
  darknessSlider.horizontalAlignment = BABYLON.GUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  darknessSlider.verticalAlignment = BABYLON.GUI.Control.VERTICAL_ALIGNMENT_CENTER;
  darknessSlider.height = "20px";
  darknessSlider.width = "200px";
  darknessSlider.minimum = -2;
  darknessSlider.maximum = 1;
  darknessSlider.value = darkness;
  darknessSlider.step = 0.01;
  darknessSlider.isVertical = false;
  darknessSlider.color = "green";
  darknessSlider.background = "grey";
  panel.addControl(darknessSlider);

  darknessSlider.onValueChangedObservable.add(function (value) {
    darkness = value;
    darknessTextBlock.text = "Darkness: " + darkness.toFixed(2);

    shadowGenerator_D._darkness = darkness;
    shadowGenerator_P._darkness = darkness;
    shadowGenerator_S._darkness = darkness;
  });

  var advancedTexture = BABYLON.GUI.AdvancedDynamicTexture.CreateFullscreenUI("UI");

  setGUI("Directional", box);
  setGUI("Spot", box2);
  setGUI("Point", box3);

  function setGUI(name, targetMesh) {
    var rect1 = new BABYLON.GUI.Rectangle();
    rect1.width = 0.15;
    rect1.height = "40px";
    rect1.cornerRadius = 20;
    rect1.color = "Orange";
    rect1.thickness = 4;
    rect1.background = "green";
    advancedTexture.addControl(rect1);
    rect1.linkWithMesh(targetMesh);
    rect1.linkOffsetY = -150;

    var label = new BABYLON.GUI.TextBlock();
    label.text = name;
    rect1.addControl(label);

    var target = new BABYLON.GUI.Ellipse();
    target.width = "20px";
    target.height = "20px";
    target.color = "Orange";
    target.thickness = 4;
    target.background = "green";
    advancedTexture.addControl(target);
    target.linkWithMesh(targetMesh);

    var line = new BABYLON.GUI.Line();
    line.lineWidth = 4;
    line.color = "Orange";
    line.y2 = 20;
    line.linkOffsetY = -10;
    advancedTexture.addControl(line);
    line.linkWithMesh(targetMesh);
    line.connectedControl = rect1;
  }

  return scene;
}
